.frameChild {
  position: absolute;
  margin: 0 !important;
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
  border-radius: var(--br-7xs);
  background: linear-gradient(
    -0.09deg,
    rgba(1, 1, 1, 0.65) 13.02%,
    rgba(217, 217, 217, 0) 70.31%
  );
  height: 229px;
  z-index: 0;
}
.sneakers {
  position: relative;
  font-weight: 500;
  display: inline-block;
  width: 147px;
  z-index: 1;
}
.frameItem,
.rectangleParent {
  border-radius: var(--br-7xs);
  height: 229px;
}

.rectangleParent:active, .rectangleParent:focus,
.rectangleGroup:active, .rectangleGroup:focus {
  outline: 3px solid #CF0A2C;
  border-radius: 30px;
}

.rectangleParent {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: var(--padding-base);
  box-sizing: border-box;
  position: relative;
  gap: var(--gap-3xs);
  background-image: url(/public/frame-2@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  cursor: pointer;
}
.frameItem {
  position: absolute;
  margin: 0 !important;
  width: 100%;
  top: 0.5px;
  right: 0;
  left: 0;
  background: linear-gradient(
    -0.09deg,
    rgba(1, 1, 1, 0.65) 13.02%,
    rgba(217, 217, 217, 0) 61.46%
  );
  z-index: 0;
}
.apparel {
  position: relative;
  font-weight: 500;
  display: inline-block;
  width: 136px;
  z-index: 1;
}
.rectangleGroup {
  flex: 1;
  border-radius: var(--br-7xs);
  height: 229px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: var(--padding-base);
  box-sizing: border-box;
  position: relative;
  gap: var(--gap-3xs);
  background-image: url(/public/frame-1@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  cursor: pointer;
}
.frameParent,
.frameWrapper {
  align-self: stretch;
  display: flex;
  align-items: center;
}
.frameParent {
  height: 274px;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-5xl);
  box-sizing: border-box;
  gap: var(--gap-mini);
}
.frameWrapper {
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--color-white);
  font-family: var(--font-proxima-nova);
}
