body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-proxima-nova: "Proxima Nova";
  --font-itc-garamond-book-condensed: "ITC Garamond Book Condensed";
  --font-inter: Inter;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-31xl: 50px;
  --font-size-sm: 14px;
  --font-size-13xl: 32px;
  --font-size-3xs: 10px;
  --font-size-3xl: 22px;
  --font-size-xl: 20px;

  /* Colors */
  --color-black: #000;
  --color-gray-100: #808080;
  --color-gray-200: #7a7a7a;
  --color-gray-300: rgba(0, 0, 0, 0);
  --color-gray-400: rgba(255, 255, 255, 0);
  --color-whitesmoke-100: #f1f1f3;
  --color-whitesmoke-200: #f1f1f2;
  --color-white: #fff;
  --color-crimson-100: #ee3348;
  --color-crimson-200: #cf0a2c;
  --color-crimson-300: #cf052c;
  --color-gainsboro: #d9d9d9;

  /* Gaps */
  --gap-lg: 18px;
  --gap-lgi: 19px;
  --gap-13xl: 32px;
  --gap-7xl: 26px;
  --gap-3xs: 10px;
  --gap-6xl: 25px;
  --gap-15xl: 34px;
  --gap-11xs: 2px;
  --gap-6xs: 7px;
  --gap-18xl: 37px;
  --gap-mini: 15px;
  --gap-32xl: 51px;
  --gap-8xs: 5px;

  /* Paddings */
  --padding-95xl: 114px;
  --padding-3xs: 10px;
  --padding-43xl: 62px;
  --padding-121xl: 140px;
  --padding-30xl: 49px;
  --padding-2xl: 21px;
  --padding-smi: 13px;
  --padding-77xl: 0px;
  --padding-101xl: 40px;
  --padding-37xl: 56px;
  --padding-5xl: 24px;
  --padding-base: 16px;
  --padding-53xl: 72px;

  /* Border radiuses */
  --br-9xs: 4px;
  --br-7xs: 6px;
}
