.newBalanceLogo2Icon {
  position: relative;
  width: 102px;
  height: 56px;
  object-fit: cover;
}
.whatsYourVibe {
  position: relative;
  display: inline-block;
  width: 290px;
}
.whichOneOf {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-xl);
  font-family: var(--font-proxima-nova);
  color: var(--color-gray-200);
}
.newBalanceLogo2Parent,
.whatsYourVibeParent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-32xl);
}
.whatsYourVibeParent {
  padding: 0 var(--padding-53xl);
}
.newBalanceLogo2Parent {
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
